import { useState, useContext } from "react";
import { Button, Grid, Stack, Typography } from "@mui/material";
import shortUUID from "short-uuid";
import { BinContext } from "./BinContext";
import { useNavigate } from "react-router-dom";

// Mock snacks data
const snacks = [
  { name: "Pizza", icon: "" },
  { name: "Burger", icon: "" },
  { name: "Fries", icon: "" },
  { name: "Hot Dog", icon: "" },
  { name: "Donut", icon: "" }
];

const LandingPage = () => {
  const { setBinId } = useContext(BinContext);
  const [selectedSnack, setSelectedSnack] = useState("");
  const navigate = useNavigate();

  const handleClick = snack => {
    setSelectedSnack(snack);
    const uuid = shortUUID.generate();
    const name = snack.name.toLowerCase();
    const binId = `${name}-${uuid}`;
    setBinId(binId);

    navigate(`/bin/${binId}`);
  };

  return (
    <Stack sx={{ mt: "20px" }}>
      <Typography variant="h4" align="center" gutterBottom>
        Welcome! Please pick your favorite snack.
      </Typography>

      <Grid container spacing={3} sx={{ justifyContent: "center" }}>
        {snacks.map(snack => (
          <Grid item key={snack.name}>
            <Button variant="contained" onClick={() => handleClick(snack)}>
              {snack.icon} {snack.name}
            </Button>
          </Grid>
        ))}
      </Grid>
    </Stack>
  );
};

export default LandingPage;
