import { useState, useEffect } from "react";
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Typography,
  Box,
  Pagination,
  Select,
  MenuItem
} from "@mui/material";
import axios from "axios";
import { useParams } from "react-router";
import Headers from "./Headers";
import Body from "./Body";

const Bin = () => {
  const { binId } = useParams();
  const [requests, setRequests] = useState([]);
  const [page, setPage] = useState(1);
  const [rowsPerPage, setRowsPerPage] = useState(10);

  const isDevelopment = process.env.NODE_ENV === "development";
  const baseUrl = isDevelopment ? "http://localhost:3001" : "";

  useEffect(() => {
    const fetchRequests = async () => {
      const response = await axios.get(`${baseUrl}/bin/${binId}`);
      console.log(response.data);

      if (Array.isArray(response.data)) {
        setRequests(response.data);
      } else {
        console.error("response.data is not an array:", response.data);
      }
    };
    fetchRequests();
  }, [binId]);

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = event => {
    setRowsPerPage(event.target.value);
  };

  return (
    <TableContainer component={Paper}>
      <Table sx={{ tableLayout: "fixed" }}>
        <TableHead>
          <TableRow>
            <TableCell sx={{ width: "10%" }}>Method</TableCell>
            <TableCell sx={{ width: "10%" }}>Path</TableCell>
            <TableCell sx={{ width: "10%" }}>Query</TableCell>
            <TableCell sx={{ width: "30%" }}>Headers</TableCell>
            <TableCell sx={{ width: "30%" }}>Body</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {requests &&
            requests.slice((page - 1) * rowsPerPage, page * rowsPerPage).map(request => (
              <TableRow key={request._id}>
                <TableCell>{request.method}</TableCell>
                <TableCell>{request.path}</TableCell>
                <TableCell>
                  <Typography>
                    {JSON.stringify(request.query) || (
                      <Typography variant="caption" sx={{ fontStyle: "italic" }}>
                        empty
                      </Typography>
                    )}
                  </Typography>
                </TableCell>
                <Headers data={request.headers} />
                <Body data={request.body} />
              </TableRow>
            ))}
        </TableBody>
      </Table>
      <Box sx={{ display: "flex", justifyContent: "space-between", padding: 2 }}>
        <Select value={rowsPerPage} onChange={handleChangeRowsPerPage}>
          <MenuItem value={10}>10</MenuItem>
          <MenuItem value={20}>20</MenuItem>
          <MenuItem value={50}>50</MenuItem>
        </Select>
        <Pagination
          count={Math.ceil(requests.length / rowsPerPage)}
          page={page}
          onChange={handleChangePage}
        />
      </Box>
    </TableContainer>
  );
};

export default Bin;
