import {
  TableCell,
  List,
  ListItem,
  Accordion,
  AccordionSummary,
  AccordionDetails,
  Typography
} from "@mui/material";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";

const Headers = ({ data }) => {
  return (
    <TableCell>
      {data && (
        <Accordion>
          <AccordionSummary expandIcon={<ExpandMoreIcon />}>
            <Typography variant="body1" color="primary">
              Headers ({Object.keys(data).length})
            </Typography>
          </AccordionSummary>
          <AccordionDetails>
            <List>
              {Object.entries(data).map(([key, value], i) => (
                <ListItem key={i}>
                  <Typography variant="body1" color="primary">
                    {key}
                  </Typography>
                  : {value}
                </ListItem>
              ))}
            </List>
          </AccordionDetails>
        </Accordion>
      )}
    </TableCell>
  );
};
export default Headers;
